.work-img{
    display: flex;
    align-items: center;
    justify-content: center;
}

.work-content{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.work-img img{
    width: 450px;
    height: 450px;
}

.workInnerContent{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 500px;
    padding: 5rem 3rem;
    background: #232323;
    border-radius: 20px;
    box-shadow: 0px 0px 10px 5px #171717;
    transition: 0.3s;
}

.workInnerContent:hover{
    box-shadow: 0px 0px 10px 10px #171717;
    cursor: pointer;
}

.work-content h2{
    color: #fff;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
}

.work-content p{
    color: #b1b1b1;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 400;
    font-size: 1.1rem;
    letter-spacing: 1px;
    text-align: center;
}

/*============================= Media Queries ==========================*/

@media (max-width: 720px){
    .abtContent{
        align-items: left;
        padding-top: 100px;
    }

    .abtImage img{
        width: 500px;
    }

    .aboutBoxWrapper{
        padding: 3rem 0rem;
    }

    .about-box{
        width: 400px;
        height: 350px;
    }

    .work-content{
        padding-bottom: 130px;
    }

    .abtContent{
        align-items:center;
    }
}

@media (max-width: 500px){
    .work-img img{
        width: 350px;
        height: 350px;
    }

    .workInnerContent{
        width: 350px;
        padding: 2rem 2rem;
    }

    .work-content{
        margin-top: 50px;
    }
}
